import axios from '../node_modules/axios';

//基础路径
axios.defaults.baseURL='http://192.168.100.166:8081';

//promise的作用：直接作用，就是把多层的嵌套回调，变成一长串的.then()方法的链式调用，主要作用就是方便。
/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url,params={}){
    return new Promise((resolve,reject) => {
      axios.get(url,{
        params:params
      })
      .then(response => {
        resolve(response.data);
      })
      .catch(err => {
        reject(err);
        console.log('get请求异常哈哈哈')
      })
    })
  }
  
  
  /**
   * 封装post请求
   * @param url
   * @param data
   * @returns {Promise}
   */
  
   export function post(url,data = {}){
     return new Promise((resolve,reject) => {
       axios.post(url,data)
            .then(response => {
              resolve(response.data);
            },err => {
              reject(err)
              console.log('post请求异常哈哈哈')
            })
     })
   }
  
